import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  baseUrl: string = "https://bms-lb-server-aubgf6h7lq-an.a.run.app/api";
  organizationRoute:string ='/organizations';
  orgUnitRoute:string ='/orgUnits'

  constructor(private http: HttpClient) { }

  getMethod(url: string){
    return this.http.get(this.baseUrl+url);
  }
  postMethod(url: string,payload:any){
    return this.http.post(this.baseUrl+url,payload);
  }
  patchMethod(url: string,payload:any){
    return this.http.patch(this.baseUrl+url,payload);
  }
  putMethod(url: string,payload:any){
    return this.http.put(this.baseUrl+url,payload);
  }

}
