import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { PositionService } from '../../services/position.service';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { startWith, map } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-orgunit-form',
  templateUrl: './orgunit-form.component.html',
  styleUrls: ['./orgunit-form.component.scss']
})
export class OrgunitFormComponent implements OnInit {
  orgFormGroup: FormGroup;
  orgAutoOptions: any[] = [];
  orgAutoFilteredOptions: Observable<string[]>;
  orgAutoControl = new FormControl();
  orgAutoSelected: string = "";

  constructor(private http: HttpService, private position: PositionService, private fb: FormBuilder) { }

  ngOnInit() {
    this.orgFormGroup = this.fb.group({
      name: ["", Validators.required],
      code: [""],
      org: new FormControl({}, Validators.compose([Validators.required])),
      mobile: [""],
      email: [""],
      addr1: [""],
      addr2: [""],
      city: ["Mumbai"],
      _2w_is_available: [false],
      _4w_is_available: [false],
      _hv_is_available: [false],
      free: [false],
      valet: [false],
      cctv: [false],
      covered: [false],
      secured: [false],
      roadside: [false],
      carwash: [false],
      charging: [false],
      _24_7: [false],
      _2w: this.fb.group({
        capacity: [""],
        min_charge: [""],
        per_hrs: [""],
        min_hrs: [""],
        per_day: [""],
        per_month: [""],
      }),
      _4w: this.fb.group({
        capacity: [""],
        min_charge: [""],
        per_hrs: [""],
        min_hrs: [""],
        per_day: [""],
        per_month: [""],
      }),
      _hv: this.fb.group({
        capacity: [""],
        min_charge: [""],
        per_hrs: [""],
        min_hrs: [""],
        per_day: [""],
        per_month: [""],
      }),
    });
    this.http.getMethod(this.http.organizationRoute).subscribe(data => {
      // console.log(data);
      this.orgAutoOptions = JSON.parse(JSON.stringify(data));
    })
    this.orgAutoFilteredOptions = this.orgFormGroup.controls["org"].valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
  }


  private _filter(value: string): string[] {
    if (typeof value === 'string') {
      const filterValue = value.toLowerCase();

      return this.orgAutoOptions.filter((option) => {
        return option.desc.toLowerCase().includes(filterValue)
      });
    } else {
      return this.orgAutoOptions;
    }

  }
  displayFn(org: any) {
    return org ? org.desc : "";
  }
  getPosition() {
    // this.position.
    this.position.getCurrentPosition().then((location: any) => {
      this.orgFormGroup.patchValue({
        longitude: JSON.stringify(location.lng),
        latitude: JSON.stringify(location.lat)
      })
    })
  }
  clearLocation() {
    // !update form group's control value
    this.orgFormGroup.controls.longitude.setValue("");
    this.orgFormGroup.controls.latitude.setValue("");
  }

  onSelectedOrg(org) {
    if (org) {
      this.orgFormGroup.controls.code.setValue(org.code.toUpperCase());
      this.orgFormGroup.controls.email.setValue(org.email.toLowerCase());
    }

  }
  submit(data) {
    this.position.getCurrentPosition().then((location: any) => {
      this.orgFormGroup.patchValue({
        longitude: JSON.stringify(location.lng),
        latitude: JSON.stringify(location.lat)
      })
      const payload = {
        desc: data.name,
        code: data.code,
        orgId: data.org._id,
        address: [{
          line1: data.addr1,
          line2: data.addr2,
          city: data.city
        }],
        mobile: data.mobile,
        city: data.city,
        email: data.email,
        type: "ME",
        parkingLotTypes: [],
        status: "P",
        location: {
          type: "Point",
          coordinates: [location.lat, location.lng]
        },
        contact: []
      };

      data.name = undefined;
      data.org = undefined;
      data.mobile = undefined;
      data.city = undefined;
      data.email = undefined;
      data.addr1 = undefined;
      data.addr2 = undefined;
      if (!data._2w_is_available) {
        data._2w = undefined;
      }
      if (!data._4w_is_available) {
        data._4w = undefined;
      }
      if (!data._hv_is_available) {
        data._hv = undefined;
      }
      let parkingLot: Object = JSON.parse(JSON.stringify(data));
      payload.parkingLotTypes.push(parkingLot);

      this.http.postMethod(this.http.orgUnitRoute, payload).subscribe(data => {
        this.clearForm();
      })

      // TODO ADD API CALL TO CREATE NEW UNIT
    });


  }

  clearForm() {
    this.orgFormGroup.reset({
      _2w_is_available: false,
      _4w_is_available: false,
      _hv_is_available: false,
      free: false,
      valet: false,
      cctv: false,
      covered: false,
      secured: false,
      roadside: false,
      carwash: false,
      charging: false,
      _24_7: false,
      city: "Mumbai"
    })
  }

}
