import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OrgunitFormComponent } from './data-form/orgunit-form/orgunit-form.component';
import { OrgFormComponent } from './data-form/org-form/org-form.component';
import { DataFormComponent } from './data-form/data-form.component';
const routes: Routes = [
  {
    path: '', component: DataFormComponent,
    children: [
      {
        path: '', component: OrgFormComponent
      },
      {
        path: 'orgUnit', component: OrgunitFormComponent
      }
    ]
  },{
    path: '**', component: DataFormComponent,
    children: [
      {
        path: '', component: OrgFormComponent
      },
      {
        path: 'orgUnit', component: OrgunitFormComponent
      }
    ]
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
